.card {
  border: 2px solid rgb(0, 0, 0);
  flex: 1 0 20%;
  margin: 10px;
  padding: 20px 20px 0;
  text-align: center;

  width: 350px;
  background: #fff2e1;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  box-shadow: 0 1px 6px rgba(51, 51, 51, 0.192);
  position: relative;
  min-height: 550px;

  img {
    padding-top: 10px;
    width: 300px;
  }

  h3 {
    margin-top: 15px;
  }

  button {
    margin-top: 30px;
    width: 150px;
    height: 51px;
    font-weight: bold;
    font-size: 1rem;
    background: rgb(255, 255, 255);
    border: 2px solid black;

    &:hover {
      cursor: pointer;

      color: white;
      background: black;
    }
  }
}
